import React, { Component } from 'react';
import {Nav,Navbar,Container,Row,Col} from 'react-bootstrap';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHome, faAddressCard, faAddressBook } from "@fortawesome/free-solid-svg-icons";
import { SocialIcon } from 'react-social-icons';
import './mynavbar.css';

export default class MyNavbar extends Component {
    constructor(props){
        super(props);
        /* activePage = 0 = Home, */
        /* activePage = 1 = About, */
        /* activePage = 2 = Contact, */                
        this.state = {
            activePage: 0,
            onHoverItem: -1
        }
        this.loadTab = this.loadTab.bind(this);
        this.setOnHoverItem = this.setOnHoverItem.bind(this);
    }

    isActiveTab = (index) => {return index === this.state.activePage}
    setOnHoverItem = (index) => {
        this.setState({
            ...this.state,
            onHoverItem: index
        })
    }

    setActiveTab = (index) => {this.setState({...this.state,activePage: index})}
    loadTab = (index, e) => {
        e.preventDefault();
        this.setActiveTab(index);
        console.log(index);
    }

    render(){
        return(
                <Container>
                    <Row>
                        <Navbar className="navbarColor" fixed="bottom">
                            <Col className="d-none d-md-block">
                                <Nav class="copyright">
                                    <span href="#">Proudly hosted by <a href="http://github.com" class="github">GitHub</a></span>
                                </Nav>
                            </Col>
                            <Col className="d-flex justify-content-center">
                                <Nav className="mynavbar">
                                    <Nav.Link className="text-center" href="#" active={this.isActiveTab(0)}
                                     onClick={(e) => this.loadTab(0, e)}
                                     onMouseEnter={(e) => this.setOnHoverItem(0)}
                                     onMouseLeave={(e) => this.setOnHoverItem(-1)}>
                                        <FontAwesomeIcon icon={faHome} color={this.state.onHoverItem === 0 && "#ffee00"}/> Home
                                    </Nav.Link>
                                    <Nav.Link className="text-center" href="#" active={this.isActiveTab(1)}
                                     onClick={(e) => this.loadTab(1, e)}
                                     onMouseEnter={(e) => this.setOnHoverItem(1)}
                                     onMouseLeave={(e) => this.setOnHoverItem(-1)}>
                                         <FontAwesomeIcon icon={faAddressCard} color={this.state.onHoverItem === 1 && "#ffee00"}/> About
                                    </Nav.Link>
                                    <Nav.Link className="text-center" href="#" active={this.isActiveTab(2)}
                                     onClick={(e) => this.loadTab(2, e)}
                                     onMouseEnter={(e) => this.setOnHoverItem(2)}
                                     onMouseLeave={(e) => this.setOnHoverItem(-1)}>
                                         <FontAwesomeIcon icon={faAddressBook} color={this.state.onHoverItem === 2 && "#ffee00"}/> Contact
                                    </Nav.Link>
                                </Nav>
                            </Col>
                            <Col className="d-none d-md-block">

                            </Col>
                        </Navbar>
                    </Row>
                </Container>
        )
    }
}